import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login'
import Home from '@/components/Home'
import user from '@/views/user'
import user1 from '@/views/user1'
import user2 from '@/views/user2'
import userList from '@/views/userList'
import equipmentList from '@/views/equipmentList'
import equipmentOrigin from '@/views/equipmentOrigin'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Login
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    { path: '/home',redirect:'/userlist', name: 'home',component: Home,hidden:false,
      children: [
        { path: '/user', name: 'user', component: user, meta: { title: '用户管理', icon: 'el-icon-setting'},
          children: [
            { path: '/user1', name: 'user1', component: user1, meta: { role: 'B', title: '用户管理1', icon: 'el-icon-setting'} },
            { path: '/user2', name: 'user2', component: user2, meta: { role: 'C', title: '用户管理2', icon: 'el-icon-setting'} }
          ]
        },
        { path: '/userlist', name: 'userlist', component: userList, meta: { title: '用户列表', icon: 'el-icon-setting'} },
        { path: '/equipmentList', name: 'equipmentList', component: equipmentList, meta: { role: 'B', title: 'B看到的', icon: 'el-icon-date' }},
        { path: '/equipmentOrigin', name: 'equipmentOrigin', component: equipmentOrigin, meta: { role: 'C', title: 'C看到的', icon: 'el-icon-star-on' }}
      ]
    }
  ]  
});


